import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import QvipleLoading from "../../../../Loader/QvipleLoading";
import style from "../LeaveAndTransferAuthority/LeavesAndTransfer.module.css";
import { useGetLeaveOverviewCount } from "../../../../hooks/member_tab/department-api";
import TileWrapper from "../../../../Department/Institute/Components/exportData/TileWrapper";
import StaffLeaveOverviewIssued from "./StaffLeaveOverviewIssued";
import { useLocation } from "react-router-dom";

function StaffLeaveOverview({ staffId }) {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [showingDataLoading, setShowingDataLoading] = useState(true);
  const [stateType, setStateType] = useState(typeList);
  const [currentTileActive, setCurrentTileActive] = useState("");

  const { getLeaveOverviewCount, getLeaveOverviewCountRefetch } =
    useGetLeaveOverviewCount({
      data: {
        sid: staffId,
      },
      skip: !staffId,
    });
  useEffect(() => {
    if (staffId) {
      setShowingDataLoading(true);
      getLeaveOverviewCountRefetch();
    }
  }, [staffId, getLeaveOverviewCountRefetch]);

  useEffect(() => {
    if (getLeaveOverviewCount?.staff) {
      let lm = typeList;
      lm[1].count = getLeaveOverviewCount?.staff?.casual_leave;
      lm[2].count = getLeaveOverviewCount?.staff?.medical_leave;
      lm[3].count = getLeaveOverviewCount?.staff?.sick_leave;
      lm[4].count = getLeaveOverviewCount?.staff?.c_off_leave;
      lm[5].count = getLeaveOverviewCount?.staff?.off_duty_leave;
      lm[6].count = getLeaveOverviewCount?.staff?.lwp_leave;

      lm[7].count = getLeaveOverviewCount?.staff?.commuted_leave;
      lm[8].count = getLeaveOverviewCount?.staff?.maternity_leave;
      lm[9].count = getLeaveOverviewCount?.staff?.paternity_leave;
      lm[10].count = getLeaveOverviewCount?.staff?.quarantine_leave;
      lm[11].count = getLeaveOverviewCount?.staff?.half_pay_leave;
      lm[12].count = getLeaveOverviewCount?.staff?.study_leave;
      lm[13].count = getLeaveOverviewCount?.staff?.sabbatical_leave;
      lm[14].count = getLeaveOverviewCount?.staff?.special_disability_leave;
      lm[15].count = getLeaveOverviewCount?.staff?.winter_vacation_leave;
      lm[16].count = getLeaveOverviewCount?.staff?.summer_vacation_leave;
      lm[17].count = getLeaveOverviewCount?.staff?.child_adoption_leave;
      lm[18].count = getLeaveOverviewCount?.staff?.bereavement_leave;

      setStateType(lm);
      if (getQuery.state.isCategory) {
        setCurrentTileActive({
          key: getQuery.state.isCategory,
        });
      } else {
        setCurrentTileActive(lm[0]);
      }
      setShowingDataLoading(false);
    }
  }, [getLeaveOverviewCount?.staff, getQuery.state.isCategory]);

  const onSelectParentType = (val) => {
    setCurrentTileActive(val);
  };
  // console.info("ksdjnfsfsd", getLeaveOverviewCount, stateType);
  return (
    <>
      <div
        className={style.request_card_inner_text}
        style={{
          marginTop: "0.5rem",
        }}
      >
        {getQuery?.state?.staffName ? (
          <h6
            style={{
              fontWeight: "400",
            }}
          >
            {t("name")} {" : "}
            {getQuery?.state?.staffName}
          </h6>
        ) : null}
        <h6
          style={{
            fontWeight: "400",
          }}
        >
          {t("total_leave_available")} {" : "}
          {getLeaveOverviewCount?.overview?.total_leave_available}
        </h6>
        <h6
          style={{
            fontWeight: "400",
          }}
        >
          {t("total_leave_taken")} {" : "}
          {getLeaveOverviewCount?.overview?.total_leave_taken}
        </h6>
      </div>
      <TileWrapper
        labelText=""
        tileList={stateType}
        activeTile={currentTileActive?.key}
        onParentTile={onSelectParentType}
        customTileContainer={{
          flexWrap: "wrap",
        }}
        customTileAvailableChild={{
          flexDirection: "column",
          alignItems: "flex-start",
        }}
      />

      <StaffLeaveOverviewIssued
        categoryKey={currentTileActive?.key}
        staffId={staffId}
      />
      {showingDataLoading && <QvipleLoading />}
    </>
  );
}

